.main_page {
  margin: 0 auto;
  padding: 10px;
  max-width: 100px;
  border: 2px solid rgb(97, 155, 179);
  border-radius: 10px;
  display: flex;
  justify-content: center;

  h1 {
    margin: 0;
  }

  p {
    text-align: center;
    border-radius: 10px;
    width: 67px;
    padding: 10px;
    background-color: rgb(97, 155, 179);

    &:last-child {
      margin-bottom: 6px;
    }
  }
}
